import React, {useEffect, useState} from 'react';
import { connect } from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {Card, Col, Row, message, Form, Input, Select, Button} from "antd";
import ApiService from "services/api.service";
import {ArrowLeftOutlined, PlusOutlined, SaveOutlined} from "@ant-design/icons";
import ThinClientSelectForm from "../../../../../components/forms/thin-client.select.form";
import {findKey} from "../../../../../../../utils";
import {useHistory} from "react-router-dom";


/**
 * @param {object} props
 * @param {object} props.type
 * @constructor
 */
function ComponentType(props){
	let { type } = props
	const [ loading, setLoading ] = useState(false)
	const [ data, setData ] = useState([])
	const [ params, setParams ] = useState({
		page:1,
		limit:100
	})
	
	
	function SelectedOption({item}){
		console.log({item})
		if(type === 'display-system' || type === 'display-system-potrait'){
			return <Select.Option value={findKey(item,['id'])} key={findKey(item,['id'])}>{findKey(item,['data','name'])}</Select.Option>
		}
		return <Select.Option value={findKey(item,['id'])} key={findKey(item,['id'])}>{findKey(item,['data','display_name']) ?? "undefined"}</Select.Option>

	}
	
	useEffect(()=> {
		setLoading(true)
		setData([])
		let url = '/api/v1/master-data/gate'
		switch(type){
			case "display-system":
			case "display-system-potrait":
				url = '/api/v1/master-data/flight-type'
				break;
			case "gate":
				url = '/api/v1/master-data/gate'
				break;
			case 'baggage-belt':
				url = '/api/v1/master-data/baggage-belt'
				break;
			default:
				break;
		}
		new ApiService({
			url:url,
			config: {
				params: {
					...params
				}
			}
		}).get()
			.then((response)=> {
				setData([...response?.data])
				setLoading(false)
			})
			.catch((err)=> {
				setLoading(false)
			})
	},[type,params])
	
	
	return (
		<Form.Item name={'type'} label={'Type'} rules={[{required:true,message:"Can't be empty!"}]}>
			<Select loading={loading}>
				{
					data.map((item,index)=> {
						return SelectedOption({item})
					})
				}
			</Select>
		</Form.Item>
	)
	
}

function mapStateToProps(state) {
	return {
	
	};
}

const rules = {
	name: [
		{
			required: true,
			message: 'Please input your category name'
		},
	],
	status: [
		{
			required: true,
			message: 'Please input your status'
		}
	],
	description: [
		{
			required: true,
			message: 'Please input your description'
		}
	],
	
}
function Index()  {
	const history = useHistory()
	
	const [loading,setLoading] = useState(false)
	const [ form ] = Form.useForm()
	const [displayType,setDisplayType] = useState('display-system')
	const onFinish = ()=> {
		setLoading(true)
		let key = 'created'
		message.loading({
			key,
			content:"Loading...",
			duration:4
		})
		form.validateFields()
			.then((value)=> {
				new ApiService({
					url:`/api/v1/`,
					body:value
				}).post()
					.then((response)=> {
						if(response?.message){
							message.success({
								key,
								content:response?.message ?? "Successfully created category",
								duration:3
							})
						}else{
							message.error({
								key,
								content:response?.message ?? "Error: created category",
								duration:3
							})
						}
						setLoading(false)
					})
					.catch((err)=> {
						message.error({
							key,
							content:err?.message ?? "Error: created category",
							duration:3
						})
						setLoading(false)
					})
			})
			.catch((err)=> {
				message.error(err?.message ?? "Some Error")
			})
	}
	
	function onChangeDisplayType(val){
		form.setFieldsValue({
			type:null
		})
		setDisplayType(val)
	}
	return (
		<React.Fragment>
			<HeroComponents
				loading={loading}
				name={"Create"}
				positionName={['Display','Settings']}
				thumbnail={null}
				extra={<div className={'d-flex align-items-center mk-gap-2'}>
					<ButtonPermission
						permission={['public','admin']}
						theme={{
							type:"disabled",
							property:{
								message:"Access Denied"
							}
						}}
						config={{
							ghost:true,
							icon:<ArrowLeftOutlined/>,
							loading:loading,
							onClick:function() {
								history.replace('/dashboard/display/settings')
							},
							type:"primary",
							size:"small"
						}}>
						Back
					</ButtonPermission>
          <ButtonPermission
            permission={['public','admin']}
            theme={{
              type:"disabled",
              property:{
                message:"Access Denied"
              }
            }}
            config={{
              ghost:false,
              icon:<SaveOutlined/>,
              loading:loading,
              onClick:onFinish,
              type:"primary",
              size:"small"
            }}>
            Save
          </ButtonPermission>
				</div>}
			/>
			
			<div style={{marginTop:90}}>
				<ContainerTailwind>
					<Card bordered={false}>
						<Form
							form={form}
							layout={'vertical'}
						>
							<Row gutter={24}>
								<Col span={12}>
									<ThinClientSelectForm name={'thin_client'} label={'Thin Client'} form={{
										rules:[{required:true,message:"Can't be empty!"}]
									}}/>
								</Col>
								<Col span={12}>
									<Form.Item name={'name'} label={'Display Name'} rules={[
										{required:true,message: "Can't be empty!"},
										({ getFieldValue }) => ({
											validator(rule, value) {
												if (getFieldValue('thin_client')) {
													return Promise.resolve();
												}
												return Promise.reject('Input your display!');
											},
										})
									]} hasFeedback>
										<Input placeholder={'Input your display name'}/>
									</Form.Item>
								</Col>
								
								<Col span={6}>
									<Form.Item name={'display_type'} label={"Type Display"} rules={[{required:true,message:"Can't be empty!"}]} hasFeedback initialValue={displayType}>
										<Select defaultValue={displayType} onChange={onChangeDisplayType}>
											<Select.Option value={'display-system'}>Display System</Select.Option>
											<Select.Option value={'display-system-potrait'}>Display System Potrait</Select.Option>
											<Select.Option value={'baggage-belt'}>Baggage Belt</Select.Option>
											<Select.Option value={'gate'}>Gate</Select.Option>
										</Select>
									</Form.Item>
								</Col>
								<Col span={6}>
									<ComponentType type={displayType}/>
								</Col>
								
								<Col span={24}>
									<Form.Item name={'remarks'} label={'Remarks'} hasFeedback rules={[]}>
										<Input.TextArea
											placeholder={'input your description'}
											showCount
											maxLength={900}
											autoSize={{ minRows: 8, maxRows: 10}}
											allowClear
											bordered={true}
											size={'small'}
										/>
									</Form.Item>
								</Col>
							</Row>
						</Form>
					</Card>
				</ContainerTailwind>
			</div>
		</React.Fragment>
	);
}

export default connect(
	mapStateToProps,
)(Index);